$primary-color: #100e11;
$secondary-color: #ffdc38;
$tertiary-color: #f9cc01;
$background-image: url('../img/background-img.png');

@mixin mouseOn {
  color: $tertiary-color;
  transition: color 0.35s;
  cursor: pointer;
}

@mixin mouseOut {
  color: white;
  transition: color 0.35s;
}

@mixin desktop {
  @media screen and (min-width: 769px) {
    @content;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
