@import "config";

  //--------------------------------  DISPLAY --------------------------------//

  .display{
    display: grid;
    grid-gap: 5px;
    grid-template-rows: 220px min-content min-content 150px min-content;
    text-align: center;
    align-items: center;
    z-index: 0;
   
    .weather-img{
      width:100%;
      height:100%;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      transition: 0.4s background-image;
    }

    .short-desc{
      h2{
        font-size: 1.5rem;
      }

      input {
        border: none;
        border-bottom: 2px dotted white;
        background: none;
        transition: border-bottom 0.35s;
        text-align: center;
        font-weight: bold;
        font-size: 1em;
        font-style: italic;
        color: white;
        white-space: pre;
        padding: 0;
        min-width: 15px;
        max-width: 220px;
      }

      .input-wrong{
        input{
          color: red;
          border-bottom: 2px dotted red;
          animation-name: bounce;
          animation-duration: .5s;
        }
        @keyframes bounce {
          0% {
            transform: translateX(0px);
            timing-function: ease-in;
          }
          37% {
            transform: translateX(5px);
            timing-function: ease-out;
          }
          55% {
            transform: translateX(-5px);
            timing-function: ease-in;
          }
          73% {
            transform: translateX(4px);
            timing-function: ease-out;
          }
          82% {
            transform: translateX(-4px);
            timing-function: ease-in;
          }
          91% {
            transform: translateX(2px);
            timing-function: ease-out;
          }
          96% {
            transform: translateX(-2px);
            timing-function: ease-in;
          }
          100% {
            transform: translateX(0px);
            timing-function: ease-in;
          }
        }
      }

      input:focus {
        outline-width: 0;
        border-bottom: 2px dotted $tertiary-color;
        transition: border-bottom 0.35s;
      }
    }

    .weather-condition{
      font-size: 1.6rem;
      text-transform: uppercase;
    }

    .temperature{
      justify-self: center;
      background-color: $tertiary-color;
      height: 120px;
      width: 120px;
      border-radius: 50%;
      position: relative;

      p{
        position: absolute;
        top: 48%;
        left: 50%;
        font-weight: bold;
        color: $primary-color;
        font-size: 1.45rem;
        transform: translateY(-48%) translateX(-50%);
      }
    }
    .buttons{
      .btn {
        justify-self: center;
        background: none;
        color: white;
        padding: 15px 30px;
        border: 2px solid white;
        width: 190px;
        outline: none;
        font-weight: bold;
        transform: skew(-12deg);
        transition: color 0.35s, border 0.35s;
        cursor: pointer;
        align-self: start;
      }
  
      .btn:hover {
        color: $tertiary-color;
        border-color: $tertiary-color;
        transition: color 0.35s, border 0.35s;
      }

      .btn:first-child{
        margin-right: 10px;
      }
    }
  }


@import "desktop";
