//desktop
@include desktop {
  body {
    nav {
      justify-content: space-between;
      color: white;

      .navbar-links {
        display: flex;
        width: 50%;
        justify-content: space-around;
        flex-direction: row;
        top: 1em;
        transform: translateX(0%);
        height: auto;

        .navbar-item {
          padding: 0;
        }
      }

      .burger {
        display: none;
      }
    }

    //--------------------------------  DISPLAY --------------------------------//

    .display{
      grid-template-columns: 400px 500px;
      grid-template-rows: repeat(4, min-content);
      grid-template-areas: 
      'img desc'
      'img cond'
      'img temp'
      'img btns';
      column-gap: 50px;
      height: auto;
      width: auto;

      @media screen and (max-width: 1000px){
        grid-template-columns: 300px 400px;
      }

      .weather-img{
        grid-area: img;
        height: 400px;
        justify-self: end;
        @media screen and (max-width: 1000px){
          height: 300px;
        }
      }

      .short-desc{
        grid-area: desc;
        h2{
          font-size: 1.8rem;
          @media screen and (max-width: 1000px){
            font-size: 1.4rem;
          }
        }
      }

      .weather-condition{
        grid-area: cond;
        font-size: 2.5rem;
        @media screen and (max-width: 1000px){
          font-size: 1.6rem;
        }
      }

      .temperature{
        grid-area: temp;
        height: 180px;
        width: 180px;
        margin-bottom: 20px;
        @media screen and (max-width: 1000px){
          height: 140px;
          width: 140px;
        }

        p{
          font-size: 3rem;
          @media screen and (max-width: 1000px){
            font-size: 2.3rem;
          }
        }
      }

      .buttons{
        grid-area: btns;
        .btn{
          font-size: 1rem;
          width: 210px;
          @media screen and (max-width: 1000px){
            font-size: 0.75rem;
            width: 170px;
          }
        }
      }
    }
  }
}
